h2 {
    margin: 15px 0 15px 0;
}

.scrollable {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-bottom: 30px;


    & .swipe {
        display: none;
    }

    & img {
        max-width: 100%;
    }
}

.comic {
    &__image {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;

        & img {
            max-width: 100%;
        }

        .mobile-img {
            display: none;
        }
    }

    &__imagesContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        margin-bottom: 30px;
        row-gap: 20px;
        column-gap: 30px;

        & {
            img {
                max-width: 100%;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .comic {
        &__image {
            .desktop-img {
                display: none;
            }

            .mobile-img {
                display: block;
                width: 100%;
            }
        }

        &__imagesContainer {
            & {
                img {
                    max-width: 100%;
                }
            }
        }

        & .scrollable {
            justify-content: flex-start;
            overflow-x: scroll;
            overflow-y: hidden;
            margin-bottom: 5px;
            padding-bottom: 15px;

            &.extra__padding {
                padding-bottom: 40px;
            }

            & img {
                max-width: none;
                max-height: 280px;
            }

            &::-webkit-scrollbar {
                display: none;
            }

            & .swipe {
                color: #fafafa;
                display: flex;
                position: absolute;
                flex-wrap: nowrap;
                align-items: center;
                gap: 5px;
                bottom: 0;
                right: 0;

                .arrow {
                    content: url("../../../assets/iconos/arrowNext.svg");
                }
            }
        }
    }
}